.image--lazy-loaded [role='img'] {
	opacity: 0;
	transition: opacity 0.4s ease-in-out;
}

.image--lazy-loaded:before {
	content: '';
	position: absolute;
	width: 100%;
	top: 20%;
	left: 0;
	height: 50%;
	opacity: .4;
	z-index: 0;
	background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	transition: opacity 0.4s ease-in-out;
}

.image--lazy-loaded.image--loaded [role='img'] {
	opacity: 1;
}

.image--lazy-loaded.image--loaded:before {
	opacity: 0;
}

